.loading-screen {
  z-index: 100;
  opacity: 1;
  background: #d3d3d3;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.loading-screen_hidden {
  opacity: 0;
  visibility: hidden;
  height: 0;
  animation: 1s linear hide-loading-screen;
}

.loading-screen__title {
  margin-top: 0;
  margin-bottom: 50px;
  font-family: Helvetica;
}

@keyframes hide-loading-screen {
  from {
    opacity: 1;
    visibility: visible;
    height: 100vh;
  }

  50% {
    opacity: 1;
    visibility: visible;
    height: 100vh;
  }

  to {
    opacity: 0;
    visibility: hidden;
    height: 100vh;
  }
}

